/* General Styles */
body {
    font-family: 'Roboto', sans-serif;
    background-color: #f0f2f5;
    color: #333;
    margin: 0;
    padding: 0;
}

h1 {
    text-align: center;
    color: #333;
    margin-top: 20px;
    font-size: 2.5rem;
}

/* Container */
.container {
    width: 100%;
    margin: 0 auto;
    padding: 20px;
}

/* Form Styles */
form {
    background: #ffffff;
    padding: 20px;
    margin: 20px 0;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

form input[type="text"],
form input[type="email"],
form input[type="file"],
form select,
form button {
    display: block;
    width: calc(100% - 40px);
    margin: 10px 20px;
    padding: 10px 15px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 1rem;
}

form input[type="text"]:focus,
form input[type="email"]:focus,
form input[type="file"]:focus,
form select:focus {
    border-color: #007bff;
    outline: none;
}

form button {
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
}

form button:hover {
    background-color: #0056b3;
}

/* Table Styles */
.table-container {
    max-width: 100%;
    margin: 20px 0;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-x: auto;
}

table {
    width: 100%;
    border-collapse: collapse;
    min-width: 700px;
}

th,
td {
    text-align: left;
    padding: 12px 15px;
    border-bottom: 1px solid #ddd;
    vertical-align: middle;
    word-wrap: break-word;
    overflow: hidden;
}

th {
    background-color: #f7f7f7;
    color: #333;
    font-weight: bold;
}

tr:nth-child(even) {
    background-color: #f9f9f9;
}

td img {
    border-radius: 4px;
    width: 50px;
    height: 50px;
    object-fit: cover;
    display: block;
    margin: 0 auto;
}

td.actions {
    display: flex;
    justify-content: space-around;
    gap: 10px;
}

td.actions button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: background-color 0.3s ease;
}

td.actions button:hover {
    background-color: #0056b3;
}

td.actions button:last-child {
    background-color: #dc3545;
}

td.actions button:last-child:hover {
    background-color: #c82333;
}

/* Responsive Adjustments */
@media (max-width: 600px) {
    form input[type="text"],
    form input[type="email"],
    form input[type="file"],
    form select,
    form button {
        width: calc(100% - 30px);
        margin: 10px 15px;
    }

    th,
    td {
        font-size: 0.9rem;
        padding: 10px;
    }

    td.actions {
        flex-direction: column;
    }

    td.actions button {
        width: 100%;
        margin: 5px 0;
    }
}
